button {
    position: relative;
    z-index: 1;
}

svg {
    width: 100%;
    height: 100%;
    margin-top: -15%;
}

.trendotron {
    background-color: #143A52;
    color: white;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 5px;
    width: 100%;
    height: 75%;
    text-align: center;
}

.trendsText {
    font-weight: 900;
    color: #CDE3EB;
}

.arrow {
    margin-top: .5%;
    position: relative;
    z-index: 1;
    font-size: 32px;
}

.arrow:hover {
    color: white;
}

.chart {
    width: 100%;
    height: 100%;
}

.choice-txt {
    font-weight: 900;
    font-size: 1.5em;
    padding-bottom: 1%;
}

.btn-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.btn-button {
    background-color: #CDE3EB;
    color: #143A52;
}

@media screen and (min-width: 1000px) {
    svg {
        margin-top: -13%;
    }
}

@media screen and (max-width: 650px) {
    svg {
        margin-top: -35%;
    }
}

@media screen and (max-width: 500px) {
    svg {
        margin-top: -60%;
    }
    
    .arrow {
        margin-top: 8%;
    }
}

@media screen and (max-width: 400px) {
    svg {
        margin-top: -50%;
    }
}




