#good {
    color: white;
}

#neutral {
    color: white;
}

#bad {
    color: white;
}

.moodbtn {
    margin: 30px;
}

.moodbtn:hover {
    font-size: 700%;
    margin: -10.5px;
    padding: 0 24px 0 24px;
    cursor: pointer;
}

.btn-choices {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

.btn-button {
    background-color: #CDE3EB;
    color: #143A52;
}

@media screen and (max-width: 700px) {
    .moodbtn {
        font-size: 350%;
        margin: 20px;
    }

    .moodbtn:hover {
        font-size: 400%;
        margin: 0;
        padding: 0 17px 0 17px;
    }
}

@media screen and (max-width: 600px) {
    .moodbtn {
        font-size: 250%;
        margin: 15px;
    }

    .moodbtn:hover {
        font-size: 300%;
        margin: 0;
        padding: 0 17px 0 17px;
    }
}


