.wrapper {
    height: 100vh;
    display: grid;
    grid-template-columns: [col1] 1fr [col2] 1fr [col3] 1fr [col4] 1fr [col5] 1fr [col6] 1fr [col7] 1fr [col8] 1fr [col9] 1fr [col10] 1fr [col11] 1fr [col12] 1fr [col13] 1fr [col14] 1fr [col15] 1fr [col16] 1fr [col17] 1fr [col18] 1fr [col19] 1fr [col20] 1fr [col21];
    grid-template-rows: [row1] 1fr [row2] 1fr [row3] 1fr [row4] 1fr [row5] 1fr [row6] 1fr [row7] 1fr [row8] 1fr [row9] 1fr [row10] 1fr [col11] 1fr [col12] 1fr [col13] 1fr [col14] 1fr [col15] 1fr [col16] 1fr [col17] 1fr [col18] 1fr [col19] 1fr [col20] 1fr [col21];
    background: -webkit-gradient(linear, left top, right bottom, from(#9BDFF5), to(#6E828A));
    background: -webkit-linear-gradient(top left, #9BDFF5, #6E828A);
    background: -o-linear-gradient(top left, #9BDFF5, #6E828A);
    background: linear-gradient(to bottom right, #9BDFF5, #6E828A);
    overflow: hidden;
    font-size: 120%;
    font-family: "Roboto";
}


